// Module
___CSS_LOADER_EXPORT___.push([module.id, `.report-container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
    background-color: #28293d; /* 배경색을 일관되게 설정 */
    border-radius: 10px; /* 패널처럼 둥근 모서리 */
    color: white; /* 텍스트 색상 */
  }
  
  .report-header {
    margin-bottom: 20px;
  }
  
  .report-header h1 {
    font-size: 24px;
    color: white; /* 제목 색상 */
  }
  
  .report-section {
    margin-bottom: 20px;
  }
  
  .report-section h2 {
    font-size: 20px;
    color: white;
    margin-bottom: 10px;
  }
  
  .report-section button,
  .report-section input[type="file"] {
    padding: 10px;
    font-size: 16px;
    background-color: #0066cc; /* 버튼의 배경색을 통일 */
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
  }
  
  .report-section button:hover,
  .report-section input[type="file"]:hover {
    background-color: #004c99; /* 버튼 호버 시 색상 변경 */
  }
  
  .report-section input[type="text"] {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 2px solid #444; /* 텍스트 입력 필드의 테두리 */
    border-radius: 5px;
    background-color: #3b3b5a; /* 배경색 */
    color: white; /* 텍스트 색상 */
    margin-bottom: 10px;
  }
  
  .report-section input[type="text"]::placeholder {
    color: #cccccc; /* 플레이스홀더 색상 */
  }
  
  .report-section .location-status {
    margin-top: 10px;
    font-size: 14px;
    color: #ff6666; /* 에러 메시지의 색상을 강조 */
  }
  
  .report-submit {
    display: flex;
    justify-content: flex-end;
  }
  
  .report-submit button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #0066cc;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
  }
  
  .report-submit button:hover {
    background-color: #004c99;
  }
